.Affiche {
    width: 300px;
    height: 169px;
    margin: 0px 8px 108px 8px;

    &_image {
        width: 100%;
        height: 100%;
        background-color: rgb(211, 68, 68);
    }

    &_info{
        position: relative;
        border-top: 1px solid black;
        bottom: 5px;
        background-color: black;
        color: white;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 10px;

        a{
            padding-top: 4px;
            font-family:Georgia, 'Times New Roman', Times, serif;
            font-weight: bold;
        }

        p{
            margin: 10px 0 0 0;
            font-weight: normal;
            font-size: 12px;
        }
    }
}

.SortIcon{
    color: rgb(202, 202, 202);
    background-color: rgba(0, 0, 0, 0.534);
    border: solid transparent 2px;
    border-radius: 50%;
    position: relative;
    top: 35px;
    left: 7px;
}

.VertIconContainer{
    background-color: transparent;
    border: none;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    
    :hover{
        cursor: pointer;
    }

    .VertIcon{
        color: rgb(233, 233, 233);
        background-color: rgba(0, 0, 0, 0.233);
        border: solid transparent 2px;
        border-radius: 50%;
    }
}